/**
 *   UI   
 */
const translateConfig = (originalConfig) => {
    let newConfig = {...originalConfig};
    newConfig.settings = {
        ...newConfig.settings,
        fieldLabel: '',
        addRuleLabel: ' ',
        addGroupLabel: ' ',
        notLabel: '',
        addCaseLabel: ' ',
        addDefaultCaseLabel: '   ',
        addSubRuleLabel: '  ',
        defaultCaseLabel: ' : ',
        fieldPlaceholder: ' ',
        funcLabel: '',
        funcPlaceholder: ' ',
        operatorLabel: '',
        operatorPlaceholder: ' ',
        valueSourcesPopupTitle: '  ',
    };
    newConfig.conjunctions.AND.label = '';
    newConfig.conjunctions.OR.label = '';
    let translation = {
        text: {
            valuePlaceholder: ' ',
            valueLabel: '',
        },
        number: {
            valuePlaceholder: ' ',
            valueLabel: '',
        },
        slider: {
            valuePlaceholder: '    ',
            valueLabel: '',
        },
        time: {
            valuePlaceholder: ' ',
            valueLabel: '',
        },
        textarea: {
            valuePlaceholder: ' ',
            valueLabel: '',
        },
        select: {
            valuePlaceholder: ' ',
            valueLabel: '',
        },
        multiselect: {
            valuePlaceholder: ' ',
            valueLabel: '',
        },
        func: {
            valuePlaceholder: ' ',
            valueLabel: '',
        },
        field: {
            valuePlaceholder: '   ',
            valueLabel: '  ',
        },
        datetime: {
            valuePlaceholder: '   ',
            valueLabel: '  ',
        },
        date: {
            valuePlaceholder: ' ',
            valueLabel: '',
        },
        boolean: {
            labelNo: '',
            labelYes: '',
        },
    };
    Object.entries(translation).forEach((entry) => {
        const [type, translatedFields] = entry;
        newConfig.widgets[type] = {
            ...newConfig.widgets[type],
            ...translatedFields,
        };
    });

    return newConfig;
};

export default translateConfig;
